@import "/../styles/variables.scss";

.account__user {
    padding: 10px;

    &:hover{
        background-color: $light !important;
        transform: translate(0px) !important;
        cursor: default;
    }
}


.user__wrapper {
    display:flex;
    flex-flow: row nowrap;
    padding: 10px 10px;
    border-radius: 10px;
    cursor: pointer;

    transition: background-color 0.5s ease-out, transform 0.5s ease-out;
    
    &:first {
        margin-top: 0;
    }

    &:hover{
        background-color: $hover;
        transform: translate(5px);
    }
}

.user__active {
    background-color: $primary;
    box-shadow: 3px 3px 10px 2px rgba(101, 136, 222, 0.51);
    cursor: default;

    &:hover{
        background-color: $primary;
        box-shadow: 3px 3px 10px 2px rgba(101, 136, 222, 0.51);
        transform: translate(0);
    }

    .user__displayname, .user__last_message, .last_message_date {
        color:$text-white
    }
}


.user__info {
    flex: 1;
    display: flex;
    flex-flow: column;
    margin-left: 10px;
    max-width: 150px;
}
.user__displayname {
    flex:1;
    font-size: 13px;
    font-style: normal;
    font-weight: 600;
    color: $text-primary;
    padding-top: 5px;
}
.user__last_message {
    flex:1;
    font-size: 11px;
    font-weight: 300;
    line-height: 15px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: $text-secondary;

}
.user__actions {
    flex: 1;
    display: flex;
    flex-direction: column;
    width: 60px;
    text-align: right; 
}

.last_message_date {
    font-size: 9px;
    font-weight: 300;
    line-height: 15px;
    color: $text-secondary;
    margin-top: auto;
    margin-bottom: auto;
}
